<template>
  <TextInput root-class="password-input" :type="inputType">
    <template v-slot:endAdornment>
      <VButton
        @mouseenter="hover = true"
        @mouseleave="hover = false"
        @click="showPassword = !showPassword"
      >
        <OpenEyeIcon v-if="showPassword" />
        <CloseEyeIcon :active="hover" v-if="!showPassword" />
      </VButton>
    </template>
  </TextInput>
</template>

<script>
import TextInput from "@/components/ui/TextInput";
import VButton from "@/components/buttons/VButton";
import OpenEyeIcon from "@/components/icons/OpenEyeIcon";
import CloseEyeIcon from "@/components/icons/CloseEyeIcon";

export default {
  name: "PasswordInput",
  components: { OpenEyeIcon, VButton, TextInput, CloseEyeIcon },
  data() {
    return {
      hover: false,
      showPassword: false,
    };
  },
  computed: {
    inputType() {
      return this.showPassword ? "text" : "password";
    },
  },
};
</script>
